import { Component, OnInit, AfterViewInit, NgZone } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { ModalController } from '@ionic/angular';

import { AppGlobalService } from './../shared/service/app-global.service';
import { EventService } from '../shared/service/event.service';
import { HttpService } from '../shared/service/http.service';
import { MapBaiduService } from '../shared/service/map.baiduapi';
declare let BMap;
@Component({
  selector: 'app-tabs',
  templateUrl: 'tabs.page.html',
  styleUrls: ['tabs.page.scss']
})
export class TabsPage implements OnInit, AfterViewInit {
  constructor(
    private http: HttpService,
    private route: ActivatedRoute,
    private router: Router,
    private appGlobalService: AppGlobalService,
    private modalController: ModalController,
    private mapBaiduService: MapBaiduService,
    private eventService: EventService,
    private ngZone: NgZone
  ) { }
  sessionId: string;
  appNavigation: any[];
  flag = 'tab1';
  defaultIndex = -111;

  // 位置对象
  positionInfo: any = {};

  ngOnInit() {
    this.mapBaiduService.refreshWatchPosition(() => {
      this.ngZone.runOutsideAngular(() => {
        this.ngZone.run(() => {
          // 以下三选一
          // web预览模式
          // this.preViewApp();
          // app指定项目id 134
          // this.debugApp(178);
          // this.debugApp(134);
          // this.debugApp(86);
          // 发布app
          this.releaseApp();
        });
      });
    });
  }
  // web预览模式
  preViewApp() {
    this.route.queryParams.subscribe(params => {
      this.sessionId = params.sessionId;
      if (!this.sessionId) {
        this.sessionId = localStorage.getItem('sessionId');
      }
      localStorage.setItem('sessionId', this.sessionId);
    });

    if (this.sessionId && this.sessionId !== 'null') {
      const params = {
        sessionId: this.sessionId,
        cityId: this.positionInfo.code
      };
      // 请求获取新数据
      console.log('getting app tab data \n');
      this.http.post('application/home/', params, res => {
        // 请求数据成功
        if (res) {
          this.getPageData(res);
        } else {
          // 请求数据失败
          this.appGlobalService.showToast(res.message, 2000, 'middle');
        }
      });

    } else {
      console.log('无法获取sessionId');
    }
  }

  /** 打包测试 116 是测试项目 id */
  debugApp(pId) {
    this.http.get('application/debug/' + pId, ress => {
      this.sessionId = ress.sessionId;
      localStorage.setItem('sessionId', this.sessionId);
      console.log(this.sessionId);
      this.http.get('application/home/' + this.sessionId, (res: any) => {
        this.getPageData(res);
      });
    });
  }
  // 发布
  releaseApp() {
    this.http.get('application/openApp/xw', ress => {
      this.sessionId = ress.sessionId;
      localStorage.setItem('sessionId', this.sessionId);
      console.log(this.sessionId);
      this.http.get('application/home/' + this.sessionId, (res: any) => {
        this.getPageData(res);
      });
    });
  }
  getPageData(res) {
    const comContainer = [];
    const pageData = JSON.parse(res.script);
    this.appNavigation = JSON.stringify(pageData.navgtion) !== '{}' ? pageData.navgtion : false;
    console.log('got app tab data \n', this.appNavigation);
    if (this.appNavigation === undefined) {
      return;
    }


    for (let i = 0; i < this.appNavigation.length; i++) {

      if (this.appNavigation[i].check) {
        console.log('defaul index ===== ', i, this.appNavigation[i].label);
        this.defaultIndex = i;
        this.getDefaultPageData(this.defaultIndex + 1, + this.appNavigation[this.defaultIndex].target);
        break;
      }
    }

    if (this.defaultIndex === -111) {
      this.defaultIndex = 0;
      this.getDefaultPageData(this.defaultIndex + 1, + this.appNavigation[this.defaultIndex].target);
    }

  }

  getDefaultPageData(index, viewId) {
    const path = 'home/tab' + (index);
    console.log('===================================default path', path);
    console.log('===========================init app city:\n', localStorage.getItem("position"));
    localStorage.setItem('tabPath', path);
    this.router.navigate([path], { queryParams: { viewId } });
  }

  change(event) {
    this.flag = event.detail.tab;
    console.log('====================================change tab', this.flag);
    if (this.flag) {
      const index = + this.flag.substring(this.flag.length - 1) - 1;
      if (index === this.defaultIndex) {
        this.defaultIndex = -111;
        return;
      }
      if (this.appNavigation && this.appNavigation[index].ifuser) {
        this.getDefaultPageData(index + 1, + this.appNavigation[index].target);
      }
    }
  }
  // 减逻辑
  setBadgeForKeyValue(key, num) {
    const index = + key.substring(key.length - 1) - 1;
    const item = this.appNavigation[index];
    const badge = + (item.badge - num);
    item.badge = badge;
    if (badge <= 0) {
      item.badge = 0;
    }
  }
  // 设置初始值
  firstBadgeForKeyValue(key, num) {
    const index = + key.substring(key.length - 1) - 1;
    const item = this.appNavigation[index];
    item.badge = num;
    if (num <= 0) {
      item.badge = 0;
    }
  }
  ngAfterViewInit(): void {
    // obj 结构{tab1:10}
    this.eventService.eventEmit.on("badgeForTab", (obj) => {
      let key = "";
      if (obj.tab1 >= 0) {
        key = 'tab1';
        this.setBadgeForKeyValue(key, obj[key]);
      }
      if (obj.tab2 >= 0) {
        key = 'tab2';
        this.setBadgeForKeyValue(key, obj[key]);
      }
      if (obj.tab3 >= 0) {
        key = 'tab3';
        this.setBadgeForKeyValue(key, obj[key]);
      }
      if (obj.tab4 >= 0) {
        key = 'tab4';
        this.setBadgeForKeyValue(key, obj[key]);
      }
      if (obj.tab5 >= 0) {
        key = 'tab5';
        this.setBadgeForKeyValue(key, obj[key]);
      }
    });

    this.eventService.eventEmit.on("firstBadgeForTab", (obj) => {
      let key = "";
      if (obj.tab1 >= 0) {
        key = 'tab1';
        this.firstBadgeForKeyValue(key, obj[key]);
      }
      if (obj.tab2 >= 0) {
        key = 'tab2';
        this.firstBadgeForKeyValue(key, obj[key]);
      }
      if (obj.tab3 >= 0) {
        key = 'tab3';
        this.firstBadgeForKeyValue(key, obj[key]);
      }
      if (obj.tab4 >= 0) {
        key = 'tab4';
        this.firstBadgeForKeyValue(key, obj[key]);
      }
      if (obj.tab5 >= 0) {
        key = 'tab5';
        this.firstBadgeForKeyValue(key, obj[key]);
      }
    });
  }
}
